.table {
    width: 100%;
    table-layout: auto;
    border-collapse: collapse;
    tr {
        border-bottom:1px solid #cecece;
    }
    tr:last-child {
        border-bottom: 0;
    }
    td {
        line-height: 1.5;
        padding: 10px 5px;
        vertical-align: top;
    }
    td:last-child {
        padding-left: 25px;
    }

}

.headerCol {
    font-weight: bold;
    min-width: 160px;
    max-width: 160px;
    width: 160px;
    text-transform: capitalize;
    
}
.valueCol {
    max-width: 400px;
    word-break: break-all;

    ul {
       padding-inline-start: 1rem;
    }
}

.fieldTable {
    tbody tr:last-child {
        border: 0;
    }
}
.fieldTableWrapper {
    margin-top:2rem;
    border-radius: 5px;
    border: 1px solid #cecece;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
    &:last-child {
        margin-bottom: 1rem;
    }
}
.fieldTableTitle {
    width: 100%;
    background: #cecece;
    text-align:left;
    text-transform: capitalize;
    padding: 10px 0 10px 5px;
    span {
        text-transform: capitalize;
    }
}