.main {
  border-radius: 5px;
  border: 1px solid #cecece;
  padding: 10px;
  margin-bottom: 1rem;
}

.titleArea {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.bodyArea {
  max-height: 0;
  overflow: hidden;
}
.bodyAreaOpen {
  max-height: unset;
}
.icon {
  transform: unset;
  transition: transform 0.4s ease-in;
}
.iconOpen {
  composes: icon;
  transform: rotate(-180deg);
}
