.main {
    --drawer-width: 240px;
    background: #9067C6;
    width: var(--drawer-width);
    height: 100%;
    position: fixed;
    transform:unset;
    transition: transform .4s ease-in;
    z-index:2000;
    box-shadow: 0 0 3px 0 rgb(0 0 0 / 12%), 0 2px 3px 0 rgb(0 0 0 / 22%);

    .button {
        position: absolute;
        left: 210px;
        z-index: 2000;
        background:none;
        max-width:24px;
        min-width:24px;
        &:hover {
            background:none
        }
        svg {
            fill:white;
        }
    }

    a, a:visited, span {
        color: white;
        text-decoration: none;
        word-break: break-all;
    }

    .linkActive span{
        font-weight:bold;
    }

    li {
        background-color: transparent;
        transition: background-color .2s ease-out;
        cursor: pointer;
    }

    li:hover {
        background-color: rgba(255,255,255,0.1);
    }

}
.mainClosed {
    transform: translateX(-205px);
    li {
        pointer-events: none;
    }
}

.wrapper {
    display: flex;
    height: 100%;
    flex-direction: column;
    padding: 26px 0;
    width: var(--drawer-width);
    transition: display 0.4s ease-in;
}
.emailGrid {
    overflow: scroll;
    span {
        padding: 0 16px;
        text-decoration: underline;
    }
    a span {
        text-decoration: none;
    }
}
.logoutGrid {
    margin-top:auto;
}