.dashboard {
  margin: auto;
  table {
    border-collapse: collapse;
    margin-top: 1em;
    td {
      padding: 5px 5px 5px 0;
    }
  }
}
.main {
  display: flex;
  height: 100%;
  width: 100%;
  position: fixed;
  left: 30px;
  overflow-x: scroll;
}
.wrapper {
  width: calc(100% - 40px);
}
.tableWrapper {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}
.report {
  width: 100%;
  height: 100%;
  .title {
    background: white;
    text-align: center;
    width: 100%;
    top: 10px;
    padding: 20px;
    h1 {
      padding: 10px 0;
      font-size: 20px;
      word-break: break-all;
    }
  }
  .body {
    margin-left: 0;
    transition: margin-left 0.4s ease-in;
    width: calc(100vw - 40px);
    position: relative;
    @media (max-width: 1200px) {
      display: flex;
      flex-direction: column;
    }
    @media (min-width: 1200px) {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
    }
  }
  .bodyOpen {
    margin-left: 200px;
    width: calc(100vw - 240px);
    .reportWrapper,
    .sdWrapper {
      @media (min-width: 1200px) {
        width: calc((100vw - 240px) / 2);
      }
    }
    .responsys {
      @media (min-width: 1200px) {
        margin-left: calc((100vw - 240px) / 2);
      }
    }
  }
}
.sourceDoc,
.responsys {
  h6 {
    padding: 0 20px;
  }
  @media (min-width: 1200px) {
    position: fixed;
    width: calc((100vw - 50px) / 2);
  }
}
.reportWrapper,
.sdWrapper {
  padding: 20px;

  @media (min-width: 1200px) {
    height: 80%;
    position: fixed;
    overflow-y: scroll;
    width: calc((100vw - 50px) / 2);
  }
}
.responsys {
  @media (min-width: 1200px) {
    margin-left: calc((100vw - 50px) / 2);
  }
}

.reportWrapper {
  padding: 20px;
}
